<template>
  <div class="container">
    <div class="user-pane">
      <div class="pane-left">
        <transition enter-active-class="rotate-diagonal-tr">
          <img src="https://avatars.githubusercontent.com/u/25954140?s=100&v=4" alt="">
        </transition>
      </div>
      <div class="pane-right">
        <span class="blogger">博主</span>
        <span class="user-name">{{ userInfo.userName }}</span>
        <p class="info-description">{{ userInfo.baseInfo }}</p>
      </div>
    </div>

    <div class="time-line">
      <el-timeline>
        <el-timeline-item :key="index" v-for="(item, index) in timeLines" :timestamp="item.time" placement="top">
          <el-card>
            <h4>{{item.title}}</h4>
            <p>dosr 更新经历： {{item.content}}</p>
          </el-card>
        </el-timeline-item>
      </el-timeline>
    </div>
  </div>
</template>

<script>
export default {
  name: 'lcz-upper-main',
  data () {
    return {
      userInfo: {
        userName: 'dosr',
        baseInfo: '男，各种瞎搞，至今一事无成。如果志同道合可以一起加个好友，一起瞎搞。'
      },
      timeLines: [
        {time: '--至今', title: '期待中', content: '期待寻找一份更具挑战性的工作，期望从事全栈开发方面为今后主要内容'},
        {time: '2020-09', title: '就职于xx公司', content: '从事javaWeb项目全站开发，承担pc端页面以及接口开发'},
        {time: '2020-07', title: '研究生毕业', content: '就读于XXX师范大学，学习多标记学习以及推荐算法学习，并兼职从事java软件开发'},
        {time: '2017-07', title: '本科毕业', content: '就读于XXX工程大学，毕业从事java软件开发'}
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  min-height: 100vh;
  width: 80%;
  min-width: 800px;
  margin: 5rem auto;
}

.user-pane {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  height: 8rem;
  padding: 0.5rem;
  box-sizing: border-box;
  min-width: 320px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

.pane-left {
  padding-left: 1rem;
  line-height: 100%;
  margin: auto;

  img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
  }

  img:hover {
    -webkit-animation: rotate-br 0.5s cubic-bezier(0.455, 0.030, 0.515, 0.955) both;
    animation: rotate-br 0.5s cubic-bezier(0.455, 0.030, 0.515, 0.955) both;
  }
}
.pane-right {
  flex: 2;
  padding-left: 1rem;
  padding-right: 0.5rem;

  .blogger {
    background-color: #909399;
    padding: 0.1rem;
    color: #ffffff;
  }

  .user-name {
    font-size: 20px;
  }

  .info-description {
    color: #8590a6;
  }
}

.time-line {
  margin-top: 1rem;
}
</style>



